'use client';

import React, { useMemo } from 'react';
import { BookOpen, Globe, ArrowRight, Database, Search } from 'lucide-react';
// Mempertahankan import data sesuai file asli Anda
import { treatyData } from '@/data/treaties';
import Link from 'next/link';

export default function EncyclopediaPage() {
  /**
   * PERBAIKAN FATAL: generateSlug
   * Fungsi ini diubah agar melakukan .trim() pada nama negara.
   * Ini memastikan link "United States - Indonesia" menjadi "...-united-states-to-indonesia"
   * bukannya "...-united-states--to-indonesia" (double dash) yang menyebabkan 404.
   */
  const generateSlug = (pair: string) => {
    if (!pair) return '#';
    
    // Menggunakan regex untuk mendukung berbagai jenis tanda hubung (dash)
    const parts = pair.split(/[-â€“â€”]/);
    if (parts.length < 2) return '#';
    
    // Melakukan trim() untuk membuang spasi yang menyebabkan link rusak
    const source = parts[0].trim().toLowerCase().replace(/\s+/g, '-');
    const target = parts[1].trim().toLowerCase().replace(/\s+/g, '-');
    
    return `/tax-treaty/tax-treaty-${source}-to-${target}`;
  };

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* HERO SECTION */}
      <section className="max-w-6xl mx-auto pt-24 pb-16 px-8 text-center space-y-6">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <BookOpen className="w-4 h-4" /> Global Jurisdictional Index
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          Tax Treaty <span className="text-[#D4AF37]">Encyclopedia.</span>
        </h1>
        <p className="max-w-2xl mx-auto text-gray-400 font-light italic text-[17px] leading-relaxed">
          The 2026 definitive directory of bilateral tax instruments. Access comprehensive data for 100+ strategic global corridors.
        </p>
      </section>

      {/* GRID DAFTAR NEGARA */}
      <section className="max-w-7xl mx-auto px-8">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {treatyData.map((item, index) => (
            <Link 
              key={index} 
              href={generateSlug(item.pair)}
              className="group bg-white border border-gray-100 p-8 rounded-[2.5rem] hover:shadow-2xl hover:shadow-gray-200/50 transition-all duration-500 flex flex-col justify-between space-y-6"
            >
              <div className="space-y-4">
                <div className="flex items-center justify-between">
                  <Globe className="text-[#D4AF37] opacity-40" size={24} />
                  <div className="px-4 py-1 bg-gray-50 rounded-full text-[9px] font-black uppercase tracking-widest text-gray-400">
                    2026 Update
                  </div>
                </div>
                {/* Menampilkan nama negara dengan pembersihan spasi yang konsisten */}
                <h3 className="text-xl font-serif italic text-black leading-tight group-hover:text-[#D4AF37] transition-colors">
                  {item.pair.split(/[-â€“â€”]/)[0]?.trim()} <br/> 
                  <span className="text-gray-300 mx-1">to</span> <br/>
                  {item.pair.split(/[-â€“â€”]/)[1]?.trim()}
                </h3>
              </div>
              
              <div className="flex items-center justify-between pt-4 border-t border-gray-50">
                <span className="text-[10px] font-black uppercase tracking-widest text-gray-400">View Treaty Details</span>
                <div className="w-10 h-10 bg-black text-[#D4AF37] rounded-full flex items-center justify-center group-hover:translate-x-2 transition-transform shadow-lg">
                  <ArrowRight size={16} />
                </div>
              </div>
            </Link>
          ))}
        </div>
      </section>

      {/* SEO FOOTER SECTION (AdSense Optimization) */}
      <section className="max-w-4xl mx-auto mt-32 px-8 text-center border-t border-gray-100 pt-20 space-y-8">
        <Database className="mx-auto text-gray-100" size={48} />
        <h4 className="text-[14px] font-black uppercase tracking-[0.4em] text-gray-300">Comprehensive Metadata Archive</h4>
        <p className="text-[15px] text-gray-400 leading-relaxed italic font-light">
          This encyclopedia serves as a public repository for standardized withholding tax rates across international borders. 
          By indexing 100+ jurisdictional pairs, GlobalTax Laboratory provides a professional-grade reference for 
          corporate executives, software architects, and legal specialists navigating the 2026 fiscal landscape.
        </p>
      </section>
    </div>
  );
}